<template>
    <v-toolbar elevation="0" class="sticky-header" style="top: 1px; background-color: #121212">
        <v-app-bar-nav-icon @click="toggle_sheet" class="hidden-md-and-up" aria-label="menu button"></v-app-bar-nav-icon>
        <v-icon x-small class="hidden-md-and-down mr-4">circle</v-icon>
        <v-toolbar-title>{{ page_title }}</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-toolbar-items class="hidden-sm-and-down"
                         v-for="(nr, i) in navigation_routes" v-bind:key="i">
            <v-divider inset vertical v-if="i !== 0"></v-divider>
            <v-btn text :to="nr.route" v-text="nr.title"></v-btn>
        </v-toolbar-items>
    </v-toolbar>
</template>

<script>
    export default {
        name: "Header",
        props: ['navigation_routes', 'sheet', 'page_title'],
        methods: {
            toggle_sheet() {
                this.$emit('toggle_sheet')
            }
        }
    }
</script>

<style scoped>
    .sticky-header {
        position: sticky;
        z-index: 10;
    }
</style>

